// Описаний в документації
import SimpleLightbox from 'simplelightbox';
// Додатковий імпорт стилів
import 'simplelightbox/dist/simple-lightbox.min.css';

// Створюємо галерею зображень

const galleryOfImages = document.querySelector('.gallery');

const fragment = document.createDocumentFragment();

images.forEach(image => {
  const listItem = document.createElement('li');
  listItem.className = 'gallery-item';

  const link = document.createElement('a');
  link.className = 'gallery-link';
  link.setAttribute('href', image.original);

  const img = document.createElement('img');
  img.className = 'gallery-image';
  img.src = image.preview;
  img.alt = image.description;

  link.append(img);
  listItem.append(link);
  fragment.append(listItem);
});

galleryOfImages.append(fragment);

// Запускаємо роботу бібліотеки SimpleLightbox

const lightbox = new SimpleLightbox('.gallery a', {
  /* options */
  captionsData: 'alt',
  captionDelay: 250,
});
